// this is the left sidebar menu component, it only appears on larger screens
import { copyrightText } from "../../data/data";
import Menu from "./Menu";

export default function Sidebar() {
	return (
		<div className="dark:bg-gray-800 invisible lg:visible fixed w-full lg:w-72 h-screen flex flex-col items-center justify-center bg-gray-100">
			<Menu />
			<p className="absolute bottom-0 text-center py-3 italic text-gray-400 dark:text-gray-600 text-sm">
				&copy; {copyrightText}
			</p>
		</div>
	);
}
