// if you want to enable toggling between light and dark theme, enable this by setting 'showThemeToggle' to true in the 'TEMPLATE_OPTIONS' section of data/data.js
export default function ModeToggle({ setDark, dark }) {
	return (
		<button
			onClick={() => setDark(!dark)}
			className="focus:outline-none rounded-none bg-black text-white opacity-50 fixed top-5 right-0 z-50 dark:bg-white dark:text-black flex h-10 w-10 items-center justify-center">
			<svg
				xmlns="http://www.w3.org/2000/svg"
				className="h-6 w-6"
				fill="none"
				viewBox="0 0 24 24"
				stroke="currentColor">
				<path
					strokeLinecap="round"
					strokeLinejoin="round"
					strokeWidth={1}
					d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z"
				/>
			</svg>
		</button>
	);
}
